/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/

.nav {
    display: flex;
    flex-wrap: wrap;
    // align-items: center;
    padding-top: 5px;
}

/* Logo
 ------------------------------------------------------------*/

.nav__brand {
    max-width: 100px;
    margin-right: 70px;
}

.nav__brand-image {
    text-align: center;
    margin-bottom: 12px;
}

.nav__brand-title  {
    margin-bottom: 0;
    font-size: 1.4rem;
    color: $brand_color;
    font-weight: normal;
    text-transform: uppercase;
}

/* Menu
 ------------------------------------------------------------*/

.main-menu {
    padding: 10px 0;
    margin: 0 10px 0 0;
    list-style: none;
    display: flex;
    flex: 0 0 820px;
    max-width: 820px;
    align-self: flex-start;
    justify-content: space-between;
}

.main-menu__item {
    position: relative;

    &:hover {
        .main-menu__link{
            &::before {
                width: 100%;
            }
        }
    }
}

.main-menu__link {
    position: relative;
    padding: 14px 0;
    display: block;
    font-size: 1.3rem;
    color: #1b253b;
    @include ubuntu_bold;  
    text-transform: uppercase;
    @include transition(color);

    &::before {
        content: attr(data-text);
        position: absolute;
        top: 50%;
        left: 0;
        width: 0;
        overflow: hidden;
        color: $font_blue;
        @include transition(width, 0.4s);
        white-space: nowrap;
        transform: translateY(-50.5%);

    }

    &::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 8px;
        width: 31px;
        height: 3px;
        opacity: 0;
        transform: translateX(-10px);
        background-color: $font_blue;
        @include transition;
    }

    &:hover {
        &::after {
            opacity: 1;
            transform: translateX(0);

        }
    }

}

.main-menu__link_active {
    color: $font_blue;

    &::after {
        opacity: 1;
        transform: translateX(0);
    }

    &:active,
    &:visited {
        color: $font_blue;
    }
}

.main-menu__item.item__has-dropdown:hover > .dropdown-menu,
.main-menu__item.item__has-dropdown:hover > .dropdown-submenu  {
    opacity: 1;
    visibility: visible;
}

.main-menu__dropdown {
    position: absolute;
    top: 45px;
    left: 0;
    padding: 0;
    margin: 0;
    list-style: none;
    border: 1px solid $border_color;
    border-radius: 4px;   
    opacity: 0;
    visibility: hidden;
    z-index: 1;
    @include transition;

    &.dropdown-menu {
        transform: translateY(30px);

        > .main-menu__item {
            &::after {
                content: '';
                width: 6px;
                height: 11px;
                display: block;
                position: absolute;
                right: 15px;
                top: 50%;
                transform: translateY(-50%);
                background-image: url('../images/icons/arrow.svg');
                background-position: center center;
                background-repeat: no-repeat;
            }

            &:hover {
                &::after {
                    background-image: url('../images/icons/arrow_hover.svg');
                }
            }
        }
    }
    
    &.dropdown-submenu {
        top: -1px;
        left: -2px;
        transform: translateX(108%);
    }

    .main-menu__item {
        width: 334px;
        border-bottom: 1px solid $border_color;

        &:last-child {
            border: none;
        }

        &:hover {
            background-color: #e4f5ff;
            
            > .main-menu__link {
                color: $font_blue;
                transform: translateX(12px);

            }
        }

    }

    .main-menu__link {
        font-size: 1.4rem;
        padding: 11px 20px 13px;
        font-weight: 400;
        text-transform: none;
        transition: transform .3s ease,
                    color .3s ease;


        &::after,
        &::before {
            content: none;
        }

    }
}

.main-menu__item.item__has-dropdown:hover > .dropdown-menu {
    transform: translateY(0);
}

.main-menu__item.item__has-dropdown:hover > .dropdown-submenu {
    transform: translateX(100%);
}

/* Search & Favorite Button
 ------------------------------------------------------------*/

.nav__search-form {
    // padding: 11px 0;
    margin: 11px 15px;
    position: relative;
}

.nav__search-btn {
    line-height: 38px;
    height: 41px;
    width: 41px;
    display: block;
    border-radius: 50%;
    border: 1px solid #a5a7ae;
    text-align: center;
    position: relative;
    z-index: 2;
    background: #fff;

    &:hover .nav__search-icon {
        fill: $font_blue;
    }
}

.nav__icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @include transition(opacity);
}

.nav__search-icon {
    fill: #a5a7ae;
    @include transition(fill);
    @include transition(opacity);
}

.nav__close-icon {
    fill: $font_blue;
    opacity: 0;
}

.nav__search-collaps {
    position: absolute;
    top: 0;
    background-color: #fff;
    height: 41px;
    width: 900px;
    right: 0;
    overflow: hidden;
    border-radius: 30px;
    
    @include transition;
}

.nav__search-collaps_disabled {
    visibility: hidden;
    opacity: 0;
}

.nav__search-area {
    height: 100%;
    position: absolute;
    right: 0;
    width: 40%;
    @include transition(transform);
}

.nav__search-area_disabled {
    transform: translateX(100%);
}

.nav__search-input {
    height: 100%;
    width: 100%;
    padding: 10px 40px 10px 30px;
    border-radius: 30px;
    border: 1px solid #a5a7ae;
}

.nav__wishlist {
    flex: 1 1 auto;
    text-align: center;
    padding-right: 8px;
    margin: auto;
    transform: translateY(-4px);

    &:hover .nav__wishlist-icon {
        opacity: 1;
    }
}

.nav__wishlist-icon {
    fill: #ff6d7e;
    opacity: 0.5;
    @include transition;
}